@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  scroll-behavior: smooth;
}

.app {
  min-height:  100vh;
  background-color: #495057;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}

@media (max-width: 700px) {
  .app {
    padding-top: 70px;
  }
}

.pageTitle{
  text-transform: uppercase;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Roboso", sans-serif;
  font-size: 3vw;
  padding: 8px;
  border-radius: 50px;
  color: #e9ecef;
}

@media (max-width: 1000px){
  .pageTitle{
    font-size: 6.4vw;
  }
}

.header {
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-transform: uppercase;
    background-color: #212529;
    font-family:  "Roboso", sans-serif ;
    font-size: 3vw;
    padding-top: 15px;
    padding-bottom: 15px;
    box-shadow: 2px 2px 2px 2px #212529;
    color: #e9ecef;
    z-index: 100;
    border-radius: 10px;
    
   

}



@media (max-width: 100px) {
    .header {
        padding-top: 15px;
        font-size:  6.4vw;
    }
}


.ContentModal_landscape {
    object-fit: contain;
    border-radius: 10px;
}

.ContentModal_portrait {
    display: none;
    object-fit: contain;
    border-radius: 10px;
}

.tagline {
    padding-bottom: 10px;
    -webkit-align-self: center;
            align-self: center;
}

.ContentModal {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: none;
    background-color: #212529;
}

.ContentModal::-webkit-scrollbar{
    display: none;
}

.ContentModal_about {
    padding: 10px;
    width: 95%;
    height: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-family: "Roboto", sans-serif;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-weight: 300;
    text-align: justify;
    
    

}

.ContentModal_title{
    height: 12%;
    font-size: 3vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    box-shadow: inset 0 0 5px #495057;
    text-align: justify;
    background-color:#495057 ;
    padding: 15px;
    border-radius: 20px;
   
   
}

.ContentModal_description {
    display: -webkit-flex;
    display: flex;
    height: 40%;
    overflow-y: scroll;
    padding: 15px;
    border-radius: 20px;
    scrollbar-width: thin; 
    box-shadow: inset 0 0 5px #495057;
    text-align: justify;
    background-color:#495057 ;


    
}

.ContentModal_description::-webkit-scrollbar{
    display: none;
}

@media (min-width:835px) {
    .ContentModal_landscape{
        display: none;
    }
    .ContentModal_portrait {
        display: -webkit-flex;
        display: flex;
        width: 38%;
    }
    .ContentModal {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        padding: 20px 0;
    }
    .ContentModal_about {
        width: 58%;
        padding: 0;
        height: 100%;
    }
    .ContentModal_title {
        font-size: 3.5vw;
    }
    .ContentModal_description {
        font-size: 22px;
        box-shadow: #000000;
        border-radius: 30px;
        
        
    }
}
.carouselItem {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    object-fit: contain;
    padding: 10px;
}

.carouselItem_img {
    border-radius: 20px;
    margin-bottom: 8px;
    box-shadow: 0px 0px 5px #212529;
}
.media {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: #212529;
    border-radius: 22px;
    position: relative;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.media:hover {
    background-color: #6c757d;
    color: black;
}

@media (max-width: 550px) {
    .media {
        width: 46%;

    }
}

.poster {
    border-radius: 40px;
    padding: 8px;

}

.title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}

.subTitle {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
    
}
.trending {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
